<template>
  <div class="containerInfoAbout animate__animated animate__fadeIn" style="animation-duration: 1s; justify-content: center; align-items: center;">
    <vs-row justify="space-around">
      <vs-alert shadow color="success" style="margin-left: 8%; z-index: 0;">
        <template #title>
            ThrillTogether
        </template>
              .                  .   .
      </vs-alert>
      <vs-button
        size="xl"
        success
        border
        style="margin-top: 6vh; margin-left: 7%;"
        @click="openNotifications"
      >
        <i class='bx bx-message-square-detail' style="margin-right: 0.5vw;"></i>   
      </vs-button>
    </vs-row>
    <vue-particles
      color="#6A4E93"
      :particleOpacity="0.4"
      :particlesNumber="50"
      shapeType="circle"
      :particleSize="4"
      linesColor="#6A4E93"
      :linesWidth="1.2"
      :lineLinked="true"
      :lineOpacity="0.5"
      :linesDistance="180"
      :moveSpeed="1.5"
      :hoverEffect="false"
      hoverMode="grab"
      :clickEffect="false"
      clickMode="push"
      style="position:absolute; z-index: -10; height: 100%; width: 100vw; left: 0%; top: 8%;"
    ></vue-particles>
  </div>
</template>

<script>
export default {
  methods: {
    openNotifications() {
      const icons = [
        "<i class='bx bx-bell'></i>",
        "<i class='bx bx-pen'></i>",
        "<i class='bx bx-edit'></i>",
        "<i class='bx bx-medal'></i>"
      ];

      const positions = [
        "bottom-right",
        "top-right",
        "top-center",
        "top-left"
      ];

      const titles = [" ", "", "", ""];

      const texts = [
        " ,      ,   .    ,    \"\"    .\n      ,       ,   ,    .",
        "          .       ,    .      ,      .",
        "      !        \" \".    ,        ,   ,   .     ,  \"  \".     !",
        "   ,     .  ,              ."
      ];

      let delay = 0;
      for (let i = 0; i < icons.length; i++) {
        setTimeout(() => {
          const noti = this.$vs.notification({
            icon: icons[i],
            duration: 20000,
            color: "dark",
            width: "500px",
            border: "danger",
            progress: 'auto',
            position: positions[i % positions.length],
            title: titles[i],
            text: texts[i],
            classes: "pulsating-notification"
          });
        }, delay);
        delay += 20000;
      }
    }
  }
};
</script>

<style>
.containerInfoAbout {
  display: flex;
  flex-direction: column;
  min-height: 40vh;
  padding-left: 16%;
  padding-right: 7%;
  padding-top: 8%;
}

.vs-notification {
  /*animation: pulsate 1.5s infinite;*/
}

@keyframes pulsate {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
</style>


